(function () {

  /* Home Directives */
  angular.module('nodeDirectives', []).
  // Directive to allow parent to have active class on nested tabbed menu
    directive('myDirec', ['$log', '$templateCache', '$compile', function ($log, $templateCache, $compile) {
      return {
        restrict: 'A',
        priority: 1000,

        link: function (scope, element, attr) {
          element.children().attr('data-toggle', 'tooltip');
          element.children().attr('data-placement', 'tooltip');
          element.children().attr('title', 'hello tool tip');

          $compile(element)(scope);
        }
      };
    }]);
}());
